/* @import "./variables.css"; */

.nav {
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  height: 90px;
  width: 100%;
  box-shadow: grey 2px 2px 5px;
  z-index: 10;
}

label.logo {
  font-size: 35px;
  line-height: 80px;
  padding: 0 50px;
  font-weight: bold;
}
label.logo span {
  font-weight: lighter;
}

label a {
  color: black;
}

.nav ul {
  margin-right: 20px;
}

.nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
  font-weight: bold;
}

.nav ul li a {
  padding: 7px 13px;
  border-radius: 3px;
  text-transform: uppercase;
}

.nav .a-nav.active,
.nav .a-nav:hover {
  background-color: rgba(252, 192, 82, 0.932);
  border-radius: 15px;
  opacity: 0.7;
  transition: 0.5s;
}

#price-title {
  font-size: 30px;
  text-shadow: rgb(128, 128, 128) 2px 0 10px;
  cursor: pointer;
}
#signup-link {
  border: whitesmoke 1px solid;
  border-radius: 5px;
  color: rgba(252, 192, 82, 0.932);
}

.checkbtn {
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}
@media (max-width: 1280px) {
  label.logo {
    font-size: 27px;
    padding-left: 25px;
  }
  nav ul li a {
    font-size: 14px;
  }
}
@media (max-width: 1148px) {
  label.logo {
    font-size: 21px;
    padding-left: 16px;
  }
  nav ul li a {
    font-size: 12px;
  }
}
@media (max-width: 1038px) {
  label.logo {
    font-size: 18px;
    padding-left: 12px;
  }
  nav ul li a {
    font-size: 12px;
  }
}
@media (max-width: 1021px) {
  label.logo {
    font-size: 16px;
    padding-left: 10px;
  }
  nav ul li a {
    font-size: 11px;
  }
}

@media (max-width: 980px) {
  .checkbtn {
    display: block;
    margin-right: 40px;
  }
  ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgba(252, 192, 82, 0.932);
    opacity: 0.9;
		top: 80px;
		left: -100%;
		text-align: center;
		transition: all 0.5s;
		z-index: 99;
		display: flex;
		flex-direction: column;
	}
	nav ul li {
		display: block;
		margin: 50px 0;
		line-height: 30px;
	}
	nav ul li a {
		font-size: 20px;
	}
	a:hover,
	a.active {
		background: none;
		color: black;
	}
	#check:checked ~ ul {
		left: 0;
	}
}
