#footer {
  height: 200px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  font-size: 12px;
  padding: 30px;
  background-color: rgba(252, 192, 82, 0.932);
  /* position: sticky;
  left: 0;
  bottom: 0; */

}

.ironhackers {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
  background-color: rgba(252, 192, 82, 0.932);
  left: 0;
  bottom: 0;
} 

.ironhackers a {
  margin-left: 20px;
  font-weight: bold;
}


@media (max-width: 700px) {
  footer * {
    font-size: 0.9em;
  }
}