.register-wrapper {
    background-image: -webkit-linear-gradient(135deg, white 30%, white 90%),
    linear-gradient(135deg, white 30%, white 90%);
    position: relative;
    z-index: 3;
    padding: 100px;
    color: #fff;
    text-align: center;
    font-size: 1rem;
    
}

.register-wrapper * {
    box-sizing: border-box;    
}

.register-wrapper form input {
    display: inline-block;
    width: 100%;
    padding: 0 20px;
    line-height: 50px;
    background: #fff;
    border: solid 1px #e6e6e6;
    border-bottom-width: 3px;
    border-radius: 5px;
    -webit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    outline: none;
}

.register-wrapper form input:not(:last-child) {
    margin-bottom: 20px;
}

.register-wrapper form input[type="email"]:focus,
.register-wrapper form input[type="email"]:active,
.register-wrapper form input[type="password"]:focus,
.register-wrapper form input[type="password"]:active {
    border-color: #ccc;
}

.register-wrapper form input[type="submit"] {
    position: relative;
    z-index: 1;
    display: inline-block;
    padding: 0 20px;
    background-color: #a74c00;
   background-image: linear-gradient(52deg, #ffe1a4 25%, #ffb170 25%, #ffb170 50%, #ffe1a4 50%, #ffe1a4 75%, #ffb170 75%, #ffb170 100%);
background-size: 101.52px 129.94px;
    border: solid 1px #C55900;
    border-bottom-width: 3px;
    color: #fff;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;   
}

.register-wrapper form input[type="submit"]:hover {
  background-image: linear-gradient(128deg, #ffb170 25%, #ffe1a4 25%, #ffe1a4 50%, #ffb170 50%, #ffb170 75%, #ffe1a4 75%, #ffe1a4 100%);
background-size: 101.52px 129.94px;
}

.register-title {
    font-size: 1.6rem;
    padding-bottom: 20px;
    margin: 0 0 20px;
    border-bottom: dashed 1px #e6e6e6;
}

.register-block {
    position: relative;
    display: block;
    width: 100%;
    max-width: 300%;
    margin: 0 auto;
    padding: 30px;
    background: rgba(252, 192, 82, 0.932);
    box-shadow: 0 0 50px rgba (0,0,0,0.2);
    border-radius: 5px;
}

.register-block::before, 
.register-block::after {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 100%;
    content: "";
    background: rgba(252, 192, 82, 0.932);
    border: solid 1px white;
    border-radius: 5px;
    transform: scale(0.9) translate(0,0);
    transition: all 0.2s ease;
}

.register-block::after {
    transition-delay: 0.05s;
}

.register-block:hover::before {
    transform: scale(0.96) translate(0,16px);
    z-index: -1;
}

.register-block:hover::after {
    transform: scale(0.92) translate(0,32px);
    z-index: -2;
}



